<template>
  <div class="contact-page" :class="type !== undefined ? 'leftAlign' : ''">
    <Navigation v-if="type === undefined" />
    <div class="main">
      <div class="header">
        Contact Us<span
          >Contact us in case you got any query or just hit us up on
          socials.</span
        >
      </div>
      <div class="contact">
        <div class="inputs">
          <input type="text" placeholder="Full Name" v-model="message.author" />
          <input type="email" placeholder="Email" v-model="message.email" />
        </div>
        <textarea
          placeholder="Message"
          rows="4"
          v-model="message.content"
        ></textarea>
        <button @click="sendMessage" :disabled="process === 'message'">
          {{ process === "message" ? "sending..." : "Send Message" }}
        </button>
      </div>
    </div>
    <Footer v-if="type === undefined" />
  </div>
</template>

<script>
import Navigation from "@/components/Navigation";
import Footer from "@/components/Footer";

export default {
  name: "Contact",
  components: {
    Navigation,
    Footer
  },
  props: ["type"],
  data() {
    return {
      process: "",
      message: {
        author: "",
        email: "",
        content: "",
        type: "contact"
      }
    };
  },
  methods: {
    async sendMessage() {
      try {
        let checkList = await Object.keys(this.message).map(e => {
          return this.message[e] === "";
        });
        if (checkList.filter(e => e).length !== 0)
          return this.$store.dispatch("setAlert", {
            message: "All fields are required.",
            type: "error"
          });
        if (
          !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
            this.message.email
          )
        )
          return this.$store.dispatch("setAlert", {
            message: "Please enter a valid email",
            type: "error"
          });
        this.$store.dispatch("setAlert", {});
        this.process = "message";
        await fetch(`/api/v1/message`, {
          method: "post",
          headers: {
            "Content-Type": "application/json"
          },
          body: JSON.stringify(this.message)
        })
          .then(response => {
            if (!response.ok) throw new Error();
            return response.json();
          })
          .catch(() => {
            throw new Error();
          });
        this.$store.dispatch("setAlert", {
          message: "Message sended, we'll contact you soon.",
          type: "success"
        });
        Object.assign(this.$data, this.$options.data.apply(this));
      } catch (error) {
        Object.assign(this.$data, this.$options.data.apply(this));
        this.$store.dispatch("setAlert", {
          message: "An error occurred, please try again",
          type: "Error"
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.main {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 1250px;
  margin: 0 auto;
  .header {
    font-size: 38px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 20px;
    span {
      display: block;
      font-size: 18px;
      color: var(--text-secondary);
    }
  }
  .contact {
    width: 60%;
    margin: 0 auto;
    text-align: center;
    .inputs {
      display: flex;
      flex-direction: row;
    }
    input,
    textarea {
      display: flex;
      flex: 1;
      width: 100%;
      padding: 12px;
      font-size: 16px;
      font-family: inherit;
      border: 0;
      border: 1.5px solid var(--text-tertiary);
      outline: none;
      font-weight: inherit;
      border-radius: 5px;
      resize: none;
      background: none;
      color: inherit;
      &::placeholder {
        color: var(--text-secondary);
      }
      &:focus {
        border-color: var(--text-secondary);
      }
      &:first-child {
        margin-right: 10px;
      }
      &:last-child {
        margin-left: 10px;
      }
    }
    textarea {
      margin: 0;
      margin-top: 15px;
    }
    button {
      display: inline-block;
      padding: 10px 15px;
      font-family: inherit;
      font-size: 16px;
      border: 1.5px solid var(--text);
      background: transparent;
      font-weight: 600;
      cursor: pointer;
      outline: none;
      border-radius: 5px;
      margin-top: 15px;
      color: inherit;
      &:hover {
        background: var(--text);
        color: var(--background);
      }
      &:active {
        transform: scale(0.9);
      }
      &:disabled {
        cursor: not-allowed;
        opacity: 0.5;
      }
    }
  }
}

.leftAlign {
  .main {
    position: relative;
    max-width: 80%;
    margin: 0;
    .header {
      width: 100%;
      margin: 0;
      text-align: left;
      font-size: 26px;
      span {
        font-size: 16px;
      }
    }
    .contact {
      width: 100% !important;
      text-align: left;
      margin: 0;
      .inputs {
        margin-top: 15px;
      }
    }
  }
}

/deep/ .footer {
  position: fixed;
}

@media only screen and (max-width: 850px) {
  .main {
    position: relative;
    margin: 15px 0;
    margin-top: 85px;
    .header {
      font-size: 30px;
      text-align: left;
      padding: 0 15px;
      margin-bottom: 0;
      width: 100%;
      span {
        font-size: 16px;
      }
    }
    .contact {
      width: 100%;
      padding: 0 15px;
      text-align: left;
      .inputs {
        flex-direction: column;
      }
      input,
      textarea {
        margin: 0;
        margin-top: 15px;
        &:first-child {
          margin-right: 0;
        }
        &:last-child {
          margin-left: 0;
        }
      }
    }
  }
  /deep/ .footer {
    position: relative;
  }
  .leftAlign {
    .main {
      max-width: 100% !important;
    }
    .header {
      padding: 0;
    }
    .contact {
      padding: 0;
    }
  }
}
</style>
