<template>
  <div>
    <Navigation />
    <div class="main">
      <Submissions v-if="$route.path === '/submission'" />
      <Submit v-if="['art', 'music', 'edit'].includes($route.params.type)" />
      <Join v-if="$route.path === '/submission/join'" type="logged" />
      <Contact v-if="$route.path === '/submission/contact'" type="logged" />
      <Team v-if="$route.path === '/submission/team'" type="logged" />
    </div>
  </div>
</template>

<script>
import Navigation from "@/components/Navigation";
import Submit from "@/components/Submit";
import Join from "@/views/Join";
import Contact from "@/views/Contact";
import Team from "@/views/Team";
import Submissions from "@/components/Submissions";
import firebase from "firebase/app";
import "firebase/auth";

export default {
  name: "Main",
  components: {
    Navigation,
    Submissions,
    Submit,
    Join,
    Contact,
    Team
  },
  data() {
    return {
      playing: null
    };
  },
  computed: {
    user() {
      return this.$store.getters.user;
    }
  },
  async mounted() {
    await this.$store.dispatch("setUser", firebase.auth().currentUser.uid);
    this.$store.dispatch("getSubmissions", this.user.flag);
  }
};
</script>

<style lang="scss" scoped>
.main {
  margin-left: 200px;
  padding: 15px;
  flex: 1;
  min-height: 100vh;
}

@media only screen and (max-width: 850px) {
  .main {
    margin-left: 0;
    padding-top: 0;
    min-height: 0;
    margin-top: 85px;
  }
}
</style>
