import Vue from "vue";
import Vuex from "vuex";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import "firebase/storage";

Vue.use(Vuex);

let store = new Vuex.Store({
  state: {
    alerts: {},
    user: {
      about: {
        city: "",
        state_povince: "",
        country: "",
        company: "",
        job_title: "",
        website: ""
      }
    },
    usernames: {},
    called: false,
    submissions: {
      Music: [],
      Art: []
    },
    messages: [],
    team: []
  },
  mutations: {
    setAlert(state, payload) {
      state.alerts = payload;
    },
    setUser(state, payload) {
      state.user = payload;
    },
    filterSubmissions(state, payload) {
      state.submissions[payload[1]].sort((a, b) =>
        payload[0].target.value === "Older" ? a.time - b.time : b.time - a.time
      );
    },
    setSubmission(state, payload) {
      state.submissions[payload.submission_type].push(payload);
      state.submissions[payload.submission_type].sort(
        (a, b) => b.time - a.time
      );
    },
    async getSubmissions(state, payload) {
      if (state.called) return;
      state.called = true;
      let ref =
        payload === 0 || payload === undefined
          ? firebase
              .firestore()
              .collection("submissions")
              .where("author", "==", firebase.auth().currentUser.uid)
          : firebase
              .firestore()
              .collection("submissions")
              .orderBy("time", "desc")
              .limit(50);
      let snapshot = await ref.get();
      if (snapshot.empty) return;
      snapshot.docs.map(async e => {
        this.dispatch(
          "setSubmission",
          Object.assign(e.data(), {
            id: e.id,
            mediaURL: await getMediaURL(e.data()),
            author: await getAuthor(e.data().author)
          })
        );
      });
    },
    async setMessages(state, payload) {
      await payload.map(async e => {
        return (e.author =
          e.type === "join" ? await getAuthor(e.author) : e.author);
      });
      payload.sort((a, b) => b.time - a.time);
      state.messages = payload;
    },
    setUsername(state, payload) {
      state.usernames = Object.assign(this.getters.usernames, payload);
    },
    updateStatus(state, payload) {
      state.submissions[payload.type].filter(
        e => e.id === payload.id
      )[0].status = payload.status;
    },
    updateMessage(state, payload) {
      if (payload[1] === "markAsRead") {
        state.messages.filter(e => e.id === payload[0])[0].unread = false;
      } else if (payload[1] === "deleteMessage") {
        let index = state.messages.findIndex(e => e.id === payload[0]);
        state.messages.splice(index, 1);
      } else if (payload[1] === "updateReplied") {
        let index = state.messages.findIndex(e => e.id === payload[0]);
        state.messages[index].replied = true;
      } else {
        null;
      }
    },
    setTeamMember(state, payload) {
      if (payload.type === "add") {
        state.team.push(payload.data);
      } else {
        let index = this.getters.team.findIndex(
          e => e.name === payload.data.name && e.role === payload.data.role
        );
        if (index !== -1) state.team.splice(index, 1);
      }
    }
  },
  actions: {
    setAlert({ commit }, payload) {
      commit("setAlert", payload);
    },
    async setUser({ commit }, payload) {
      let doc = await firebase
        .firestore()
        .collection("users")
        .doc(payload)
        .get();
      if (!doc.exists) return;
      let flag = await firebase
        .auth()
        .currentUser.getIdTokenResult()
        .then(idTokenResult => {
          return idTokenResult.claims.flag;
        });
      commit(
        "setUser",
        Object.assign(doc.data(), {
          flag: flag === undefined ? 0 : flag,
          about:
            doc.data().about === undefined
              ? {
                  city: "",
                  state_povince: "",
                  country: "",
                  company: "",
                  job_title: "",
                  website: ""
                }
              : doc.data().about
        })
      );
    },
    filterSubmissions({ commit }, payload) {
      commit("filterSubmissions", payload);
    },
    setSubmission({ commit }, payload) {
      commit("setSubmission", payload);
    },
    getSubmissions({ commit }, payload) {
      commit("getSubmissions", payload);
    },
    setUsername({ commit }, payload) {
      commit("setUsername", payload);
    },
    updateStatus({ commit }, payload) {
      commit("updateStatus", payload);
    },
    updateMessage({ commit }, payload) {
      commit("updateMessage", payload);
    },
    async getMessages({ commit }, payload) {
      if (this.getters.messages.length > 0) return;
      let ref =
        payload === 0 || payload === undefined
          ? firebase
              .firestore()
              .collection("messages")
              .where("reply_to", "==", firebase.auth().currentUser.uid)
              .where("type", "==", "reply")
          : firebase
              .firestore()
              .collection("messages")
              .orderBy("type")
              .where("type", "!=", "reply")
              .limit(50);
      let snapshot = await ref.get();
      if (snapshot.empty) return;
      commit(
        "setMessages",
        snapshot.docs.map(e =>
          Object.assign(e.data(), {
            id: e.id
          })
        )
      );
    },
    setTeamMember({ commit }, payload) {
      commit("setTeamMember", payload);
    },
    async getTeam({ commit }) {
      if (this.getters.team.length > 0) return;
      let doc = await firebase
        .firestore()
        .collection("team")
        .doc("all")
        .get();
      if (!doc.exists) return;
      doc.data().all.map(e => {
        commit("setTeamMember", { type: "add", data: e });
      });
    }
  },
  getters: {
    user: state => state.user,
    submissions: state => state.submissions,
    usernames: state => state.usernames,
    alerts: state => state.alerts,
    messages: state => state.messages,
    team: state => state.team
  }
});

async function getMediaURL(data) {
  if (data.mediaId === null) return null;
  let url = await firebase
    .storage()
    .refFromURL(
      `gs://heyoowebapp.appspot.com/${data.submission_type}/${data.author}/${data.mediaId}`
    )
    .getDownloadURL();
  return url;
}

async function getAuthor(id) {
  if (store.getters.usernames[id] !== undefined)
    return store.getters.usernames[id];
  let doc = await firebase
    .firestore()
    .collection("users")
    .doc(id)
    .get();
  store.dispatch("setUsername", { [id]: doc.data() });
  return doc.data();
}

export default store;
