<template>
  <div class="join" :class="type !== undefined ? 'leftAlign' : ''">
    <Navigation v-if="type === undefined" />
    <div class="main">
      <div class="header">
        Join Us<span>Join us to change the way people hear music.</span>
      </div>
      <div class="contact">
        <div class="inputs">
          <input
            type="text"
            placeholder="Name"
            v-model.number="message.author"
          />
          <input type="email" placeholder="Email" v-model="message.email" />
        </div>
        <div class="inputs">
          <input
            type="number"
            placeholder="Age"
            v-model.number="message.content.age"
          />
          <input
            type="text"
            placeholder="Location"
            v-model="message.content.location"
          />
        </div>
        <textarea
          placeholder="Social Media Handles (eg. twitter.com/abc, instagram.com/abc)"
          v-model="message.content.socials"
        ></textarea>
        <textarea
          placeholder="What makes you passionate about 7Cerebrum?"
          v-model="message.content.questions[0].answer"
        ></textarea>
        <textarea
          placeholder="Tell us a little bit about yourself and what can you help us with?"
          v-model="message.content.questions[1].answer"
        ></textarea>
        <button @click="submit" :disabled="process === 'submiting'">
          {{ process === "submiting" ? "submiting..." : "Submit" }}
        </button>
      </div>
    </div>
    <Footer v-if="type === undefined" />
  </div>
</template>

<script>
import Navigation from "@/components/Navigation";
import Footer from "@/components/Footer";

export default {
  name: "Join",
  components: {
    Navigation,
    Footer
  },
  props: ["type"],
  data() {
    return {
      process: "",
      message: {
        type: "join",
        content: {
          author: "",
          email: "",
          age: "",
          location: "",
          socials: "",
          questions: [
            {
              question: "What makes you passionate about 7Cerebrum?",
              answer: ""
            },
            {
              question:
                "Tell us a little bit about yourself and what can you help us with?",
              answer: ""
            }
          ]
        }
      }
    };
  },
  methods: {
    async submit() {
      try {
        let checkList = await Object.keys(this.message).map(e => {
          return e === "content"
            ? Object.keys(this.message[e]).map(i => {
                return i === "questions"
                  ? this.message[e][i].map(x => !x.answer)
                  : !this.message[e][i];
              })
            : !this.message[e];
        });
        if (checkList.flat(2).filter(e => !e).length !== 8)
          return this.$store.dispatch("setAlert", {
            message: "All fields required.",
            type: "Error"
          });
        this.$store.dispatch("setAlert", {});
        this.process = "submiting";
        await fetch(`/api/v1/message`, {
          method: "post",
          headers: {
            "Content-Type": "application/json"
          },
          body: JSON.stringify(this.message)
        })
          .then(response => {
            if (!response.ok) throw new Error();
            return response.json();
          })
          .catch(() => {
            throw new Error();
          });
        this.$store.dispatch("setAlert", {
          message: "Message sended, We'll contact you soon.",
          type: "success"
        });
        Object.assign(this.$data, this.$options.data.apply(this));
      } catch (error) {
        Object.assign(this.$data, this.$options.data.apply(this));
        this.$store.dispatch("setAlert", {
          message: "An error occurred, please try again",
          type: "Error"
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.main {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 1250px;
  margin: 0 auto;
  .header {
    font-size: 38px;
    font-weight: 700;
    text-align: center;
    span {
      display: block;
      font-size: 18px;
      color: var(--text-secondary);
    }
  }
  .contact {
    width: 60%;
    margin: 0 auto;
    text-align: center;
    .inputs {
      display: flex;
      flex-direction: row;
      margin-top: 20px;
    }
    input,
    textarea {
      display: flex;
      flex: 1;
      width: 100%;
      padding: 12px;
      font-size: 16px;
      font-family: inherit;
      border: 0;
      background: transparent;
      border: 1.5px solid var(--text-tertiary);
      outline: none;
      font-weight: inherit;
      border-radius: 5px;
      resize: none;
      color: inherit;
      &::placeholder {
        color: var(--text-secondary);
      }
      &:focus {
        border-color: var(--text-secondary);
      }
      &:first-child {
        margin-right: 10px;
      }
      &:last-child {
        margin-left: 10px;
      }
    }
    textarea {
      margin: 0;
      margin-top: 20px;
    }
    button {
      display: inline-block;
      padding: 10px 15px;
      font-family: inherit;
      font-size: 16px;
      border: 1.5px solid var(--text);
      background: transparent;
      font-weight: 600;
      cursor: pointer;
      outline: none;
      border-radius: 5px;
      color: inherit;
      margin-top: 20px;
      &:hover {
        background: var(--text);
        color: var(--background);
      }
      &:active {
        transform: scale(0.9);
      }
      &:disabled {
        cursor: not-allowed;
        opacity: 0.5;
      }
    }
  }
}

.leftAlign {
  .main {
    position: relative;
    max-width: 80%;
    margin: 0;
    .header {
      width: 100%;
      margin: 0;
      text-align: left;
      font-size: 26px;
      span {
        font-size: 16px;
      }
    }
    .contact {
      width: 100% !important;
      text-align: left;
      margin: 0;
    }
  }
}

/deep/ .footer {
  position: fixed;
}

@media only screen and (max-width: 850px) {
  .main {
    position: relative;
    margin: 15px 0;
    margin-top: 85px;
    width: 100%;
    .header {
      font-size: 30px;
      text-align: left;
      padding: 0 15px;
      margin-bottom: 0;
      width: 100%;
      span {
        font-size: 16px;
      }
    }
    .contact {
      width: 100%;
      padding: 0 15px;
      text-align: left;
      .inputs {
        flex-direction: column;
        margin: 0;
      }
      input,
      textarea {
        margin: 0;
        margin-top: 15px;
        &:first-child {
          margin-right: 0;
        }
        &:last-child {
          margin-left: 0;
        }
      }
    }
  }
  /deep/ .footer {
    position: relative;
  }
  .leftAlign {
    .main {
      max-width: 100% !important;
    }
    .header {
      padding: 0;
    }
    .contact {
      padding: 0;
    }
  }
}
</style>
