<template>
  <div class="modal">
    <div class="title">
      {{
        route === "edit"
          ? "Edit Account"
          : route === "art"
          ? "Submit Art"
          : "Submit Music"
      }}
      <span v-if="route === 'edit'">
        This information helps use reach out to you. Make sure you keep them up
        to date.
      </span>
    </div>
    <section v-if="route === 'edit'">
      <div class="sub-title">My Account</div>
      <div class="group">
        <div>
          <label>Full Name</label>
          <input
            class="input"
            type="text"
            placeholder="Name"
            v-model="user.name"
          />
        </div>
        <div>
          <label>Email</label>
          <input
            class="input"
            type="text"
            placeholder="Email"
            v-model="user.email"
          />
        </div>
        <div v-if="email_changed">
          <label>Password</label>
          <input
            class="input"
            type="password"
            placeholder="Password"
            v-model="email_password_change"
          />
        </div>
      </div>
      <div class="group">
        <div>
          <label>City (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="City"
            v-model="user.about.city"
          />
        </div>
        <div>
          <label>State/Povince (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="State/Povince"
            v-model="user.about.state_povince"
          />
        </div>
        <div>
          <label>Country (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="Country"
            v-model="user.about.country"
          />
        </div>
      </div>
      <div class="group">
        <div>
          <label>Job Title (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="Job Title"
            v-model="user.about.job_title"
          />
        </div>
        <div>
          <label>Company (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="Company"
            v-model="user.about.company"
          />
        </div>
        <div>
          <label>Website (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="Website"
            v-model="user.about.website"
          />
        </div>
      </div>
      <div class="buttons">
        <button :disabled="process === 'saving'" @click="saveUserInfo">
          {{ process === "saving" ? "saving..." : "Save" }}
        </button>
      </div>
      <div class="sub-title">Change Password</div>
      <div class="group">
        <div>
          <label
            >Old Password<i
              :class="password_visible_old ? 'eye-close' : 'eye-open'"
              @click="password_visible_old = !password_visible_old"
            ></i
          ></label>
          <input
            class="input"
            :type="password_visible_old ? 'text' : 'password'"
            placeholder="Old Password"
            v-model="old_password"
          />
        </div>
        <div>
          <label
            >New Password
            <i
              :class="password_visible_new ? 'eye-close' : 'eye-open'"
              @click="password_visible_new = !password_visible_new"
            ></i>
          </label>
          <input
            class="input"
            :type="password_visible_new ? 'text' : 'password'"
            placeholder="New Password"
            v-model="new_password"
          />
        </div>
      </div>
      <div class="buttons">
        <button
          :disabled="process === 'change_password'"
          @click="change_password"
        >
          {{
            process === "change_password" ? "changing..." : "Change Password"
          }}
        </button>
      </div>
    </section>
    <section v-if="route === 'music' || route === 'art'">
      <div class="sub-title">Details</div>
      <div class="group">
        <div>
          <label>Artist Name</label>
          <input
            class="input"
            type="text"
            placeholder="Artist Name"
            v-model="$data[route].artist_name"
          />
        </div>
        <div>
          <label>{{ route === "art" ? "Art" : "Song" }} Title</label>
          <input
            class="input"
            type="text"
            :placeholder="route === 'art' ? 'Art Title' : 'Song Title'"
            v-model="$data[route].title"
          />
        </div>
      </div>
      <div class="group" v-if="route === 'music'">
        <div>
          <label>Album Title (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="Album Title"
            v-model="$data[route].optional_album_title"
          />
        </div>
        <div>
          <label>Release Date</label>
          <input class="input" type="date" v-model="$data[route].release" />
        </div>
      </div>
      <div class="group" v-if="route === 'music'">
        <div>
          <label>Record Label (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="Record Label"
            v-model="$data[route].optional_record_label"
          />
        </div>
        <div>
          <label>Purchase URL (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="Website URL"
            v-model="$data[route].optional_purchase_url"
          />
        </div>
      </div>
      <div class="group">
        <div>
          <label>{{ route === "art" ? "Art Type" : "Music Genre" }}</label>
          <select
            class="input"
            v-if="route === 'art'"
            v-model="$data[route].type"
          >
            <option>Illustration</option>
            <option>Photograph</option>
          </select>
          <select
            class="input"
            v-if="route === 'music'"
            v-model="$data[route].type"
          >
            <option v-for="genre in genres" :key="genre">{{ genre }}</option>
          </select>
        </div>
        <div>
          <label>Choose {{ route === "art" ? "Art" : "Music" }}</label>
          <label for="file" class="input" style="cursor:pointer" ref="file"
            >Click here to Choose File
            {{ route === "art" ? "(1920x1080px)" : "(320kbps)" }}</label
          >
          <input
            id="file"
            type="file"
            style="display:none"
            ref="fileInput"
            @change="changeMedia"
            :accept="route === 'art' ? 'image/jpeg, image/png' : 'audio/mp3'"
          />
        </div>
      </div>
      <div class="sub-title">Socials</div>
      <div class="group">
        <div>
          <label>Spotify {{ route === "music" ? "" : "(optional)" }}</label>
          <input
            class="input"
            type="text"
            placeholder="https://open.spotify.com/artist/abc"
            v-model="$data[route].socials.spotify"
          />
        </div>
        <div>
          <label>Soundcloud (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="https://soundcloud.com/abc"
            v-model="$data[route].socials.soundcloud"
          />
        </div>
      </div>
      <div class="group">
        <div>
          <label>Twitter (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="Twitter Username"
            v-model="$data[route].socials.twitter"
          />
        </div>
        <div>
          <label>Instagram (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="Instagram Username"
            v-model="$data[route].socials.instagram"
          />
        </div>
      </div>
      <div class="group">
        <div>
          <label>YouTube (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="https://youtube.com/channel/abc"
            v-model="$data[route].socials.youtube"
          />
        </div>
        <div>
          <label>Website (optional)</label>
          <input
            class="input"
            type="text"
            placeholder="https://yourwebsite.com"
            v-model="$data[route].socials.website"
          />
        </div>
      </div>
    </section>
    <div class="buttons">
      <button
        v-if="route === 'art'"
        :disabled="process === 'Art'"
        @click="saveMedia('Art')"
      >
        {{ process === "Art" ? "submitting..." : "Submit Art" }}
      </button>
      <button
        v-if="route === 'music'"
        :disabled="process === 'Music'"
        @click="saveMedia('Music')"
      >
        {{ process === "Music" ? "submitting..." : "Submit Music" }}
      </button>
    </div>
  </div>
</template>

<script>
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import "firebase/storage";

export default {
  name: "Submit",
  data() {
    return {
      process: "",
      email_password_change: "",
      new_password: "",
      old_password: "",
      email_changed: false,
      password_visible_old: false,
      password_visible_new: false,
      media: { name: "", mime: "", url: "" },
      genres: require("@/assets/genres.json"),
      art: {
        title: "",
        submission_type: "Art",
        type: "Illustration",
        artist_name: "",
        socials: {
          spotify: "",
          youtube: "",
          twitter: "",
          instagram: "",
          soundcloud: "",
          website: ""
        }
      },
      music: {
        title: "",
        submission_type: "Music",
        type: "Acoustic",
        optional_album_title: "",
        release: "",
        optional_record_label: "",
        optional_purchase_url: "",
        artist_name: "",
        socials: {
          spotify: "",
          youtube: "",
          twitter: "",
          instagram: "",
          soundcloud: "",
          website: ""
        }
      }
    };
  },
  computed: {
    user() {
      return this.$store.getters.user;
    },
    route() {
      return this.$route.params.type;
    }
  },
  watch: {
    route() {
      Object.assign(this.$data, this.$options.data.apply(this));
      if (this.$refs.fileInput !== undefined) {
        this.$refs.fileInput.value = null;
        this.$refs.file.innerHTML = `Click here to Choose File ${
          this.route === "art" ? "(1920x1080px)" : "(320kbps)"
        }`;
      }
    },
    "user.email"(newEmail) {
      this.email_changed = !(newEmail === firebase.auth().currentUser.email);
    }
  },
  methods: {
    setMedia(payload) {
      this.media.name = payload.name;
      this.media.mime = payload.type;
      this.media.url = URL.createObjectURL(payload);
      this.$refs.file.innerHTML = payload.name;
    },
    changeMedia(e) {
      this.$store.dispatch("setAlert", {});
      if (e.target.files[0]) {
        if (e.target.files[0].type.startsWith("image")) {
          let img = new Image();
          img.src = URL.createObjectURL(e.target.files[0]);
          img.onload = () => {
            if (img.width !== 1920 && img.height !== 1080)
              return this.$store.dispatch("setAlert", {
                message: "You can only upload images of dimension 1920x1080.",
                type: "error"
              });
            URL.revokeObjectURL(img.src);
            this.setMedia(e.target.files[0]);
          };
        } else {
          let audio = new Audio();
          audio.src = URL.createObjectURL(e.target.files[0]);
          audio.oncanplaythrough = () => {
            let kbit = e.target.files[0].size / 128;
            if (Math.ceil(Math.round(kbit / audio.duration) / 16) * 16 !== 320)
              return this.$store.dispatch("setAlert", {
                message: "You can only upload music with a bitrate of 320kbps.",
                type: "error"
              });
            URL.revokeObjectURL(audio.src);
            this.setMedia(e.target.files[0]);
          };
        }
      }
    },
    async saveMedia(type) {
      try {
        let time = Date.now();
        let checkList = await Object.keys(this.$data[type.toLowerCase()]).map(
          e => {
            return (
              !e.startsWith("optional_") &&
              this.$data[type.toLowerCase()][e] === ""
            );
          }
        );
        if (
          checkList.filter(e => e).length !== 0 ||
          this.media.url === "" ||
          (type === "Music" &&
            this.$data[type.toLowerCase()].socials.spotify === "")
        )
          return this.$store.dispatch("setAlert", {
            message: "All fields except optionals are required.",
            type: "error"
          });
        this.$store.dispatch("setAlert", {});
        this.process = type;
        let ref = firebase
          .storage()
          .ref()
          .child(
            `${type}/${this.user.uid}/${time}.${this.media.name
              .split(".")
              .pop()}`
          );
        let blob = await fetch(this.media.url).then(response => {
          return response.blob();
        });
        await ref.put(blob, {
          contentType: this.media.mime
        });
        await firebase
          .firestore()
          .collection("submissions")
          .doc()
          .set(
            Object.assign(this.$data[type.toLowerCase()], {
              author: this.user.uid,
              mediaId: `${time}.${this.media.name.split(".").pop()}`,
              status: "Under Review",
              time: time
            })
          );
        this.$store.dispatch(
          "setSubmission",
          Object.assign(this.$data[type.toLowerCase()], {
            author: this.user,
            mediaURL: this.media.url,
            status: "Under Review",
            time: time
          })
        );
        if (this.$refs.fileInput !== undefined) {
          this.$refs.fileInput.value = null;
          this.$refs.file.innerHTML = `Click here to Choose File ${
            this.route === "art" ? "(1920x1080px)" : "(320kbps)"
          }`;
        }
        Object.assign(this.$data, this.$options.data.apply(this));
        this.$store.dispatch("setAlert", {
          message: "Submission submitted.",
          type: "success"
        });
      } catch (error) {
        URL.revokeObjectURL(this.media.url);
        if (this.$refs.fileInput !== undefined) {
          this.$refs.fileInput.value = null;
          this.$refs.file.innerHTML = `Click here to Choose File ${
            this.route === "art" ? "(1920x1080px)" : "(320kbps)"
          }`;
        }
        Object.assign(this.$data, this.$options.data.apply(this));
        this.$store.dispatch("setAlert", {
          message: "An error occurred, please try again",
          type: "error"
        });
      }
    },
    async saveAccount() {
      try {
        console.log(Object.assign(this.user, { updated: Date.now() }));
        await firebase
          .firestore()
          .collection("users")
          .doc(firebase.auth().currentUser.uid)
          .update(Object.assign(this.user, { updated: Date.now() }));
        await firebase.auth().currentUser.updateProfile({
          displayName: this.user.name
        });
        Object.assign(this.$data, this.$options.data.apply(this));
        this.$store.dispatch("setAlert", {
          message: "Account updated.",
          type: "success"
        });
      } catch (error) {
        console.log(error);
        Object.assign(this.$data, this.$options.data.apply(this));
        this.$store.dispatch("setAlert", {
          message: "An error occurred, please try again",
          type: "error"
        });
      }
    },
    async saveUserInfo() {
      try {
        if (
          !this.user.name ||
          !this.user.email ||
          (this.email_changed && !this.email_password_change)
        )
          return this.$store.dispatch("setAlert", {
            message: "All fields except optional are required.",
            type: "error"
          });
        this.process = "saving";
        if (this.email_changed) {
          const user = firebase.auth().currentUser;
          const credential = firebase.auth.EmailAuthProvider.credential(
            user.email,
            this.email_password_change
          );
          user
            .reauthenticateWithCredential(credential)
            .then(() => {
              user
                .updateEmail(this.user.email)
                .then(() => {
                  this.saveAccount();
                })
                .catch(() => {
                  this.user.email = firebase.auth().currentUser.email;
                  Object.assign(this.$data, this.$options.data.apply(this));
                  this.$store.dispatch("setAlert", {
                    message: "An error occurred, please try again",
                    type: "error"
                  });
                });
            })
            .catch(error => {
              this.user.email = firebase.auth().currentUser.email;
              this.$store.dispatch("setAlert", {
                message: error.message,
                type: "error"
              });
              Object.assign(this.$data, this.$options.data.apply(this));
            });
        } else {
          this.saveAccount();
        }
      } catch (error) {
        Object.assign(this.$data, this.$options.data.apply(this));
        this.$store.dispatch("setAlert", {
          message: "An error occurred, please try again",
          type: "error"
        });
      }
    },
    async change_password() {
      try {
        if (!this.old_password || !this.new_password)
          return this.$store.dispatch("setAlert", {
            message: "All fields are required.",
            type: "error"
          });
        this.process = "change_password";
        const user = firebase.auth().currentUser;
        const credential = firebase.auth.EmailAuthProvider.credential(
          user.email,
          this.old_password
        );
        user
          .reauthenticateWithCredential(credential)
          .then(() => {
            user
              .updatePassword(this.new_password)
              .then(() => {
                this.$store.dispatch("setAlert", {
                  message: "Password succesfully changed.",
                  type: "success"
                });
                Object.assign(this.$data, this.$options.data.apply(this));
              })
              .catch(() => {
                Object.assign(this.$data, this.$options.data.apply(this));
                this.$store.dispatch("setAlert", {
                  message: "An error occurred, please try again",
                  type: "error"
                });
              });
          })
          .catch(error => {
            this.$store.dispatch("setAlert", {
              message: error.message,
              type: "error"
            });
            Object.assign(this.$data, this.$options.data.apply(this));
          });
      } catch (error) {
        Object.assign(this.$data, this.$options.data.apply(this));
        this.$store.dispatch("setAlert", {
          message: "An error occurred, please try again",
          type: "error"
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.modal {
  display: flex;
  flex-direction: column;
  .buttons {
    display: flex;
    margin: 0;
    margin-top: 15px;
    button {
      &:first-child {
        background: transparent;
        color: inherit;
        &:hover {
          background: var(--text);
          color: var(--background);
        }
      }
    }
  }
}

.title {
  font-size: 26px;
  font-weight: 700;
  span {
    color: var(--text-secondary);
    font-size: 16px;
    display: block;
  }
}

.sub-title {
  font-size: 18px;
  margin-top: 15px;
}

.group {
  display: flex;
  flex-direction: row;
  width: 80%;
  margin-top: 15px;
  div {
    flex: 1;
    margin-right: 15px;
    label {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 5px;
      color: var(--text-secondary);
      i {
        width: 20px;
        height: 20px;
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
      }
    }
    &:only-child {
      margin: 0;
    }
    &:last-child {
      margin: 0;
    }
  }
}

@media only screen and (max-width: 850px) {
  .group {
    width: 100%;
    flex-direction: column;
    margin-top: 0;
    div {
      margin: 0;
      label {
        margin-top: 15px;
      }
      .input {
        margin: 0;
      }
      &:first-child {
        margin-right: 0;
      }
    }
  }
}
</style>
