<template>
  <div id="app">
    <router-view />
    <div class="cover" v-if="state === 'offline'">
      <div>
        <div class="icon offline"></div>
        You seem to be offline.
      </div>
    </div>
    <div class="cover" v-if="updateExists">
      <div>
        <div class="icon download"></div>
        An update is available.<br /><br />
        <div class="buttons" style="display:inline-block;margin:0 auto;">
          <button class="button" @click="refreshApp" style="margin:0 auto;">
            Update App
          </button>
        </div>
      </div>
    </div>
    <transition name="fade">
      <div v-if="Object.keys(alerts).length > 0" class="error_outer">
        <div class="error_inner" :class="alerts.type">
          <div>{{ alerts.message }}</div>
          <button @click="$store.dispatch('setAlert', {})"></button>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import update from "@/mixins/update";

export default {
  name: "App",
  mixins: [update],
  data() {
    return {
      state: "",
      darkMode: localStorage.getItem("theme") === "dark"
    };
  },
  computed: {
    alerts() {
      return this.$store.getters.alerts;
    }
  },
  watch: {
    darkMode: function() {
      this.checkTheme();
    }
  },
  methods: {
    checkTheme() {
      let htmlElement = document.documentElement;
      if (this.darkMode) {
        localStorage.setItem("theme", "dark");
        htmlElement.setAttribute("theme", "dark");
      } else {
        localStorage.setItem("theme", "light");
        htmlElement.setAttribute("theme", "light");
      }
    }
  },
  mounted() {
    this.checkTheme();
    window.addEventListener("offline", () => {
      this.state = "offline";
    });
    window.addEventListener("online", () => {
      this.state = "";
    });
  }
};
</script>

